import React from 'react';
// import logo from './logo.svg';
import './App.css';
import Particles from 'react-particles-js'

function App() {
  return (
    <div className="App">
      <header className="App-header">
        <img src={require("./logo.png")} alt="logo" width={300} />
        <p>
          Portfolio coming soon...
        </p>
        
        {/* <a
          className="App-link"
          href="https://reactjs.org"
          target="_blank"
          rel="noopener noreferrer"
        >
          Learn React
        </a> */}
      </header>
      <span className="credits">Photo by <a href="https://unsplash.com/@andyjh07?utm_source=unsplash&amp;utm_medium=referral&amp;utm_content=creditCopyText">Andy Holmes</a> on <a href="https://unsplash.com/images/nature/space?utm_source=unsplash&amp;utm_medium=referral&amp;utm_content=creditCopyText">Unsplash</a></span>
      <Particles style={{width: "100vw", height: "100vh", position: "absolute" , top: "0", left: "0"}}
        params={{ 
          particles: { 
            number: { 
              value: 80, 
              density: { 
                enable: true, 
                value_area: 1000, 
              },
            },
            opacity: {
              value: 0.5
            },
            line_linked: {
              shadow: {
                enable: true,
                color: "#3CA9D1",
                blur: 5
              },
            },
            links: {
              opacity: 0.2
            }
          },
          "interactivity": {
            "events": {
                "onhover": {
                    "enable": true,
                    "mode": "repulse"
                }
            }
        } 
        }} 
      />
    </div>
  );
}

export default App;
